import React from "react";
import "../styles/PageAddProduct.scss";

class PageAddProduct extends React.Component {
  state = {
    name: "",
    type: "pieczywo"
  };

  handleChange = event => {
    // check if the imput consists only white spaces
    // if it does do not update the state with it
    if(!event.target.value.trim().length) return    

    this.setState({
      [event.target.name]: event.target.value.toLowerCase()
    });
  };

  render() {
    return (
      <div className="formWrapper">
        <form
          onSubmit={event => {
            event.preventDefault();
            this.setState({
              name: "",
              type: "pieczywo"
            });
            this.props.handleAddProduct(this.state);
          }}
          className="form"
        >
          {/* Input z nazwa produktu */}
          <label htmlFor="name" className="form__label">
            Wpisz nazwƒô produktu
          </label>

          <input
            type="text"
            id="name"
            name="name"
            value={this.state.name}
            onChange={this.handleChange}
            required
            placeholder="np.chleb tostowy"
            className="form__input"
          />
          {/* Input z dzia≈Çem danego produktu */}
          <label htmlFor="type" className="form__label">
            Podaj dzia≈Ç
          </label>

          <select
            id="type"
            name="type"
            value={this.state.type}
            onChange={this.handleChange}
            className="form__input"
          >
            <option value="pieczywo">Pieczywo</option>
            <option value="owoce i warzywa">Owoce i warzywa</option>
            <option value="nabia≈Ç">Nabia≈Ç</option>
            <option value="miƒôsa i sery">Miƒôsa i sery</option>
            <option value="mro≈ºonki i przyprawy">Mro≈ºonki i przyprawy</option>
            <option value="s≈Çoiki i puszki">Puszki i s≈Çoiki</option>
            <option value="kawa i herbata">Kawa i herbata</option>
            <option value="makarony">Makarony i ry≈º</option>
            <option value="s≈Çodycze">S≈Çodycze</option>
            <option value="≈õrodki czysto≈õci">≈örodki czysto≈õci</option>
            <option value="napoje">Napoje</option>
            <option value="alkohole">Alkohole</option>
            <option value="art.papiernicze">Art.papiernicze</option>
            <option value="kosmetyki">Kosmetyki</option>
            <option value="ubrania">Ubrania</option>
          </select>

          <button className="form__button">Dodaj produkt</button>
        </form>
      </div>
    );
  }
}

export default PageAddProduct;
